import React, { Component } from "react";
import Api from "./api";

export default class Teacher extends Component {
  state = {
    teachers: []
  };

  componentDidMount() {
    this.loadTeacher();
  }

  loadTeacher = async () => {
    const response = await Api.get("/teacher");
    this.setState({ teachers: response.data });
  };

  renderTableData() {
    return this.state.teachers.map((teachers, index) => {
      const {
        id,
        name,
        email,
        state,
        city,
        school_name,
        graduation_university_name,
        graduation_year,
        work_school_name
      } = teachers;
      return (
        <tr key={id}>
          <td>{name}</td>
          <td>{email}</td>
          <td>{state}</td>
          <td>{city}</td>
          <td>{school_name}</td>
          <td>{graduation_university_name}</td>
          <td>{graduation_year}</td>
          <td>{work_school_name}</td>
        </tr>
      );
    });
  }

  render() {
    return (
      <table className="table">
        <thead>
          <tr>
            <th>Nome</th>
            <th>E-mail</th>
            <th>Estado</th>
            <th>Cidade</th>
            <th>Nome da Escola</th>
            <th>Nome da Universidade</th>
            <th>Ano de Graduação</th>
            <th>Escola Onde Trabalha</th>
          </tr>
        </thead>
        <tbody>
          {this.renderTableData()}
        </tbody>
      </table>
    );
  }
}
