import React, { Component } from "react";
import Api from "./api";

export default class Alunos extends Component {
  state = {
    alunos: []
  };

  componentDidMount() {
    this.loadAlunos();
  }

  loadAlunos = async () => {
    const response = await Api.get("/alunos");
    this.setState({ alunos: response.data });
  };

  renderTableData() {
    return this.state.alunos.map((aluno, index) => {
      const { name, email, state, city, school_name } = aluno;
      return (
        <tr>
          <td>{name}</td>
          <td>{email}</td>
          <td>{state}</td>
          <td>{city}</td>
          <td>{school_name}</td>
        </tr>
      );
    });
  }

  render() {
    return (
      <table className="table">
        <thead>
          <tr>
            <th>Nome</th>
            <th>E-mail</th>
            <th>Estado</th>
            <th>Cidade</th>
            <th>Nome da Escola</th>
          </tr>
        </thead>
        <tbody>
            {this.renderTableData()}
        </tbody>
      </table>
    );
  }
}
