import React, { Component } from "react";
import Api from "./api";

export default class Aulas extends Component {
  state = {
    aulas: []
  };

  componentDidMount() {
    this.loadAulas();
  }

  loadAulas = async () => {
    const response = await Api.get("/aulas");
    this.setState({ aulas: response.data });
  };

  renderTableData() {
    return this.state.aulas.map(aulas => {
      const { id, title, content, link, name_teacher } = aulas;
      return (
        <tr key={id}>
          <td>{title}</td>
          <td>{content}</td>
          <td>
            <a href={link}><i className="fa fa-link"></i></a>
          </td>
          <td>{name_teacher}</td>
        </tr>
      );
    });
  }

  render() {
    return (
      <table className="table">
        <thead>
          <tr>
            <th>Titulo</th>
            <th>Conteudo</th>
            <th>Link</th>
            <th>Nome do Professor </th>
          </tr>
        </thead>
        <tbody>{this.renderTableData()}</tbody>
      </table>
    );
  }
}
